'use client';

import * as React from 'react';
import { cn } from '@/lib/utils';

export interface ButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  fullWidth?: boolean;
  isLoading?: boolean;
  variant?: 'primary' | 'ghost' | 'secondary';
  size?: 'sm' | 'md' | 'lg';
  iconLeft?: React.ReactNode;
  iconRight?: React.ReactNode;
}

export const Button = React.forwardRef<HTMLButtonElement, ButtonProps>(
  (
    {
      className,
      children,
      fullWidth,
      isLoading,
      variant = 'primary',
      size = 'md',
      iconLeft,
      iconRight,
      disabled,
      ...props
    },
    ref
  ) => {
    // Clases base según variante
    const variantClasses = {
      primary: 'bg-gradient-to-r from-purple-600 to-blue-600 text-white hover:from-purple-700 hover:to-blue-700',
      secondary: 'bg-gray-200 text-gray-900 hover:bg-gray-300',
      ghost: 'bg-transparent border border-secondary-300 text-neutral-800 hover:bg-secondary-100',
    };

    // Clases según tamaño
    const sizeClasses = {
      sm: 'text-sm py-2 px-3',
      md: 'text-base py-3 px-5',
      lg: 'text-lg py-4 px-6',
    };

    return (
      <button
        ref={ref}
        className={cn(
          "rounded-xl font-bold transition-all duration-300 shadow-[0_5px_25px_rgba(0,0,0,0.4)] hover:shadow-[0_5px_35px_rgba(0,0,0,0.6)] flex items-center justify-center gap-2",
          variantClasses[variant],
          sizeClasses[size],
          fullWidth && 'w-full',
          (isLoading || disabled) && 'opacity-60 cursor-not-allowed',
          className
        )}
        disabled={isLoading || disabled}
        {...props}
      >
        {isLoading ? (
          <span className="animate-spin">⏳ Cargando...</span>
        ) : (
          <>
            {iconLeft && <span className="flex items-center">{iconLeft}</span>}
            {children}
            {iconRight && <span className="flex items-center">{iconRight}</span>}
          </>
        )}
      </button>
    );
  }
);

Button.displayName = 'Button';
